.header .welcome {
  display: flex;
  flex-direction: column;
  margin-top: 78px; }
  .header .welcome__hey {
    width: 100%;
    height: 44px;
    font-weight: bold;
    font-size: 32px;
    line-height: 44px; }
  .header .welcome__lets-try {
    height: 44px;
