.search-wrapper {
  margin-top: 10px;
  margin-bottom: 20px; }

.form-search {
  display: block;
  max-width: 680px;
  width: 100%;
  padding: 10px 15px;
  border: 2px solid #333333;
  border-radius: 10px;
  color: #333333;
  font-size: 1rem; }
  .form-search::-webkit-input-placeholder {
    font-size: 16px;
    line-height: 22px;
    color: #ABABAB; }
  .form-search:-ms-input-placeholder {
    font-size: 16px;
    line-height: 22px;
