    color: #ABABAB; }

.categories-list__category {
  padding-left: 0;
  padding-right: 10px;
  margin-top: 10px; }
  .categories-list__category input {
    position: absolute;
    left: 0;
    z-index: -1;
    width: 1rem;
    height: 1.25rem;
    opacity: 0;
    overflow: visible;
    margin: 0; }
  .categories-list__category label {
    padding: 5px 12px;
    font-weight: 500;
    font-size: 12px;
    line-height: 16px;
    letter-spacing: 2px;
    text-transform: uppercase;
    color: #ABABAB;
    border: 2px solid #F8F8F8;
    border-radius: 6px;
    cursor: pointer; }
  .categories-list__category input:checked + label {
    color: #333333;
    border-color: #F8F8F8;
    background-color: #F8F8F8; }
  .categories-list__category input:not(:disabled):not(:checked) + label:hover {
    border-color: #333333;
    color: #333333;
    opacity: 0.8; }
  .categories-list__category input:not(:disabled):active + label {
    background-color: #ABABAB;
    border-color: #ABABAB; }
  .categories-list__category input:focus:not(:checked) + label {
    border-color: #333333; }

.categories-list {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  padding-left: 0;
  padding-right: 10px;
  margin-bottom: 12px; }

.jokes-form {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  margin-top: 45px; }
  .jokes-form__component {
    position: relative;
    margin-bottom: 8px; }
  .jokes-form .custom-checkbox {
    position: absolute;
    left: 0;
    z-index: -1;
    width: 1rem;
    height: 1.25rem;
    opacity: 0;
    overflow: visible;
    margin: 0; }
  .jokes-form .custom-checkbox + label {
    position: relative;
    font-size: 18px;
    line-height: 26px;
    cursor: pointer;
    display: inline-flex;
    align-items: center;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none; }
  .jokes-form .custom-checkbox + label::before {
    content: '';
    display: inline-block;
    width: 20px;
    height: 20px;
    flex-shrink: 0;
    flex-grow: 0;
    border: 2px solid #ABABAB;
    border-radius: 50%;
    margin-right: 15px;
    background-repeat: no-repeat;
    background-position: center center;
    background-size: 50% 50%;
    cursor: pointer; }
  .jokes-form .custom-checkbox:checked + label::before {
    color: #333333;
    border-color: #333333;
    background-color: #FFFFFF;
    background-image: url("data:image/svg+xml;charset=utf-8,%3Csvg xmlns='http://www.w3.org/2000/svg' width='10' height='10' viewBox='-4 -4 8 8'%3E%3Ccircle r='4' fill='333333'/%3E%3C/svg%3E"); }
  .jokes-form .custom-checkbox:not(:disabled):not(:checked) + label:hover::before {
    border-color: #333333; }
  .jokes-form .custom-checkbox:not(:disabled):active + label::before {
    background-color: #ABABAB;
    border-color: #ABABAB; }
  .jokes-form .custom-checkbox:focus + label::before {
    box-shadow: 0 0 0 0.2rem rgba(171, 171, 171, 0.3); }
  .jokes-form .custom-checkbox:focus:not(:checked) + label::before {
    border-color: #333333; }
  .jokes-form .btn {
    margin-top: 12px;
    max-width: 152px;
    min-height: 42px;
    font-weight: bold;
    font-size: 16px;
    line-height: 22px;
    background: linear-gradient(92.01deg, #8EA7FF 0%, #7291FF 100%);
    border: none;
    border-radius: 10px;
    color: #FFFFFF;
    cursor: pointer; }
  .jokes-form .btn:hover {
    background: rgba(38, 143, 255, 0.7); }

.active {
  background-color: red; }

.unactive {
